import React from "react";
import { Link } from "react-router-dom";

const SiteNav = () => {
    return (
        <React.Fragment>
            <nav
                className="navbar navbar-expand-md navbar-dark bg-dark justify-content-end"
                aria-label="Fourth navbar example"
            >
                <ul className="nav">
                    <li className="nav-item">
                        <Link className="nav-link" to="/">Home</Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" href="#" to="/Projects">Projects</Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" href="#" to="/About">About Me</Link>
                    </li>
                </ul>
            </nav>
        </React.Fragment>
    )
}
export default SiteNav;