import React from 'react';
import { SocialIcon } from 'react-social-icons';


const Footer = () => {
    return (
        <React.Fragment>
            <div className="container">
                <footer className="d-flex flex-wrap justify-content-between align-items-center py-3 my-4 border-top">
                    <div className="col-md-4 d-flex align-items-center">
                        <a href="/" className="mb-3 me-2 mb-md-0 text-muted text-decoration-none lh-1">

                        </a>
                        <span class="mb-3 mb-md-0 text-muted">&copy; Jordan Watts</span>
                    </div>

                    <ul class="nav col-md-4 justify-content-end list-unstyled d-flex">
                        <li className="ms-3"><SocialIcon url="https://twitter.com" /></li>
                        <li className="ms-3"><SocialIcon url="https://github.com/cashmereskeleton" /></li>
                        <li className="ms-3"><SocialIcon url="https://instagram.com" /></li>
                    </ul>
                </footer>
            </div>
        </React.Fragment>
    )
}

export default Footer;